.profile__author {
  font-weight: 500;
  font-size: 42px;
  color: #fff;
  line-height: 56px;
  margin: 0 auto 0 0;
  max-width: 540px;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}

@media (max-width : 1023px) {
  .profile__author {
    max-width: 340px;
  }
}

@media (max-width : 767px) {
  .profile__author {
    margin: 0 auto;
  }
}

@media (max-width : 500px) {
  .profile__author {
    max-width: 250px;
    font-size: 27px;
  }
}
